@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300&display=swap');

.navbar {
  grid-column: 1 / 4;
  grid-row: 1;
  z-index: 9;
  height: 50px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: space-between;
  align-items: center;

  margin: 0 30px 0 30px;

  * {
    font-family: 'Roboto Slab', serif;
    color: #ffffff;
    text-decoration: none;
  }
}

.logoLink {
  line-height: 30px;
  align-content: center;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;

  h3 {
    padding-left: 10px;
  }
}

.logo {
  padding-left: 20px;
  max-width: 30px;
}

.links {
  list-style: none;
}

.link {
  display: inline-block;
  padding-right: 20px;

}