.Header {
  grid-column: 1 / 4;
  grid-row: 1 / 3;

  background: center center no-repeat;
  background-size: cover;
  margin-bottom: 50px;
  color: #ffffff;

  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: center;
  align-items: center;
}

.mainBlock {
  padding: 150px 0;
  text-align: center;
}

.title {
  font-size: 80px;
}

.separator {
  border-width: 4px;
  border-color: #eee;
  border-top: 1px solid #eee;
  border-left: 1px solid #eee;
  max-width: 100px;
  margin: 15px auto;
}

.description {
  font-size: 24px;
  font-weight: 300;
  line-height: 1.1;
  display: block;
}
